.footer-menu-list {
   display: flex;
   gap: 35px;

   @media (max-width: 992px) {
      justify-content: center;
      margin-bottom: 30px;
      flex-wrap: wrap;
   }
}

.footer-menu-link {
   white-space: nowrap;
   transition: color 0.3s ease;

   &:hover {
      color: $mainColor3  !important;
   }
}

.footer-icon-list {
   display: flex;
   gap: 40px;
   justify-content: flex-end;

   @media (max-width: 992px) {
      justify-content: center;
      margin-bottom: 30px;
   }
}

.footer-icon-link {
   transition: transform 0.3s ease;

   &:hover {
      transform: scale(1.2);
   }
}

